import React from "react";
import Card from "react-bootstrap/Card";
import NickPw from "./nickPw";

import HashTag from "./hashtag"
import WriteArea from "./textarea";
import Button from "react-bootstrap/Button";
import RadioBt from "./radioBt";

function alertClicked() {
    alert('  .   .');
}

const RvForm = () => (
    <>
        <Card>
            <div style={{textAlign:"center"}}>
            <h4 style={{marginTop:"20px"}}>  |    </h4>
            <h6 style={{marginBottom:"20px"}}>?</h6>
            </div>
            <NickPw></NickPw>

            <h4 style={{textAlign:"center", marginTop:"50px"}}> ?</h4>
            <RadioBt></RadioBt>

            <h4 style={{textAlign:"center", marginTop:"50px"}}>    </h4>
            <HashTag></HashTag>

            <h4 style={{textAlign:"center",marginTop:"50px",marginBottom:"30px"}}> </h4>
            <WriteArea></WriteArea>
            <Button variant="outline-secondary" style={{display:"block",margin:"auto",marginTop:"30px",width:"300px",marginBottom:"30px"}} onClick={alertClicked} href="/review/check"></Button>
        </Card>
    </>
);

export default RvForm;