import React from 'react';
import Card from 'react-bootstrap/Card'
import Button from 'react-bootstrap/Button'
import Container from 'react-bootstrap/Container'
import Col from 'react-bootstrap/Col'
import Row from 'react-bootstrap/Row'
import Image from 'react-bootstrap/Image'
import NaviagtionBar from "../../navbar";
import '../emotion.css'
import Footer from "../../footer/Footer";
import Share from "./Share/Result10";

class TenHappy extends React.Component {
    render() {
        return (
            <div>
                <NaviagtionBar/>
                <h1></h1>
                <Col xs={12} md={12}>
                    <Image style={{
                        display: "block",
                        margin: "auto",
                        width: "280px",
                        height: "420px",
                        objectFit: "cover",
                        overflow: "hidden",
                        marginBottom: "50px"
                    }}
                           src="/images/10.png"></Image>
                </Col>

                <div className="wrap">
                    <div className="text">
                        <br></br>     ! <br/>
                         ? <br/>
                         . <br/>
                              ? <br/>
                        <br></br>
                    </div>
                    <Share></Share>
                </div>

                <h1 className="recwrap"></h1>

                <Container style={{marginTop:"-50px",marginBottom:"300px"}}>
                    <div className="flip-container" onTouchStart="this.classList.toggle('hover');"
                         style={{width: '18rem', display: 'block', marginRight:"auto",marginLeft:"auto"}}>
                        <div className="flipper">
                            <div className="front">
                                <Card className="card" style={{width: '18rem', display: 'inline-block'}}>
                                    <br/>
                                    <Card.Img style={{width: "150px"}} variant="top" src="/images/Glutamic_acid.png"/>
                                    <br/>
                                    <Card.Body>
                                        <br/>
                                        <Card.Title></Card.Title>
                                        <br/>
                                    </Card.Body> </Card>

                            </div>
                            <div className="back">
                                <Card className="card" style={{width: '18rem', display: 'inline-block'}}>
                                    <Card.Body>
                                        <Card.Title></Card.Title>
                                        <Card.Text>
                                            <br/>
                                             <br/>
                                             <br/>
                                              <br/>
                                              . <br/><br/>
                                        </Card.Text>
                                    </Card.Body> </Card>
                            </div>
                        </div>
                    </div>
                </Container>

                <Container style={{marginBottom:"300px"}}>
                    <div className="flip-container" onTouchStart="this.classList.toggle('hover');"
                         style={{width: '18rem', display: 'block', marginRight:"auto",marginLeft:"auto"}}>
                        <div className="flipper">
                            <div className="front">
                                <Card className="card" style={{width: '18rem', display: 'inline-block'}}>
                                    <Card.Img style={{width: "150px"}} variant="top" src="/images/Caffeine.png"/>
                                    <Card.Body>
                                        <Card.Title></Card.Title>
                                    </Card.Body> </Card>
                            </div>
                            <div className="back">
                                <Card className="card" style={{width: '18rem', display: 'inline-block'}}>
                                    <Card.Body>
                                        <Card.Title></Card.Title>
                                        <Card.Text>
                                            <br/>
                                             <br/>
                                             <br/>
                                             <br/>
                                             . <br/><br/>
                                        </Card.Text>
                                    </Card.Body> </Card>

                            </div>
                        </div>
                    </div>
                </Container>

                <Container style={{marginTop:"570px",marginBottom:"900px"}}>
                    <div className="flip-container" onTouchStart="this.classList.toggle('hover');"
                         style={{width: '18rem', display: 'block', marginRight:"auto",marginLeft:"auto"}}>
                        <div className="flipper">
                            <div className="front">
                                <Card className="card" style={{width: '18rem', display: 'inline-block'}}>
                                    <br/>
                                    <Card.Img style={{width: "150px"}} variant="top" src="/images/Serotonin.png"/>
                                    <Card.Body>
                                        <Card.Title></Card.Title>
                                    </Card.Body> </Card>
                            </div>
                            <div className="back">
                                <Card className="card" style={{width: '18rem', display: 'inline-block'}}>
                                    <Card.Body>
                                        <Card.Title></Card.Title>
                                        <Card.Text>
                                            <br/>
                                               <br/>
                                              <br/>
                                              <br/>
                                               <br/>
                                              . <br/>
                                        </Card.Text>
                                    </Card.Body> </Card>
                            </div>
                        </div>
                    </div>
                </Container>






                <div className="wrap">
                    <h1 className="food"></h1>
                    <Container>
                        <Row>
                            <Col xs={6} md={6}>
                                <Image src="/images/udon.jpeg" rounded
                                       style={{width: '13rem', display: 'inline-block'}}/>
                                <h6 className="ttokbokki"></h6>
                            </Col>
                            <Col xs={6} md={6}>
                                <Image src="/images/pasta.jpg" rounded
                                       style={{width: '20rem', display: 'inline-block'}}/>
                                <h6 className="ramen"></h6>
                            </Col>
                        </Row>
                    </Container>
                </div>


                <Button className="button" variant="outline-secondary" href={"/restaurant/10"} style={{
                    marginBottom: '30px',
                    marginLeft: 'auto',
                    marginRight: 'auto',
                    display: 'block',
                    width: "300px"
                }}>    </Button>{' '}
                <Footer></Footer>
            </div>
        );
    }
}

export default TenHappy;
