import React from "react";
import axios from "axios";
import {toast} from "react-toastify";
import {Field, Form, Formik} from "formik";
import './reviewform.css';

const RvForm = (props: any) => {
    const submit = async (values: any) => {
        console.log(values);
        const {hashtag} = values;
        try {
            await axios.post('http://mimi-project.kr:5000/api/user', {hashtag});

            toast.success(' ', {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            props.history.push('/login');
        } catch (e) {
            toast.error('.  ', {
                position: "top-center",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
        }
    }
    return (
        <>
            <Formik initialValues={{
                checked: []
            }}
                    onSubmit={async (values) => {
                        alert(JSON.stringify(values, null, 2));
                    }}>

                {({ values }) => (
                    <Form>
                        <div role="group" aria-labelledby="checkbox-group">
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                            <label>
                                <Field type="checkbox" name="checked" value="" />
                                #
                            </label>
                        </div>
                    </Form>
                )}
            </Formik>
        </>
    );
}
export default RvForm;